
import axios from 'axios'

if (process.env.NODE_ENV == 'development') {
  axios.defaults.baseURL = '/api'
} 
else if (process.env.NODE_ENV == 'test') {
  axios.defaults.baseURL = 'https://api.kele8.cn/agent/http://test998/'
}
 else if (process.env.NODE_ENV == 'production') {
  axios.defaults.baseURL = 'https://api.kele8.cn/agent/http://47.114.147.221/'
}
else if (process.env.NODE_ENV == 'build') {
  axios.defaults.baseURL = 'https://api.kele8.cn/agent/http://build/demo'
}


axios.defaults.timeout = 6000


export function get (url) {
  return new Promise((resolve, reject) => {
    axios.get(url)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}
