<template>
  <div class="about">
    <div class="back" @click="back">
      <img class="auto-img" src="../../assets/images/return-details.png" alt />
    </div>
    <div class="img_box">
      <img :src="data.cover" @error="imgErr" class="img" />
    </div>
    <div class="item_box">
      <van-row class="positionR">
        <van-col span="7" class="about_img">
          <img :src="data.cover" />
        </van-col>
        <van-col span="17">
          <div class="tit_box">
            <h3 class="title textOverflow">{{ data.name }}</h3>
            <p class="txt">
              <van-icon name="user-o" />
              {{ data.author }}
            </p>
            <p class="txt">
              <van-icon name="underway-o" />
              {{ data.time }}
            </p>
          </div>
          <div class="btn_box">
  
            <div class="favorite" @click="likeAndNotLike">
              <span class="icon-favorite" :class="{ active: isLike }"></span>
              <span class="text">{{ favoriteText }}</span>
            </div>

         
            <van-button round color="#49dda1" @click="start" type="info"
              >观看{{ startJson.name }}</van-button
            >
          </div>
        </van-col>
      </van-row>
    </div>
    <div :class="['tips tips_box', isAllShowTips ? '' : 'isShow']">
      <span class="jianjie" @click="isAllShowTips = !isAllShowTips"
        >查看简介</span
      >{{ data.introduce }}<br />
      <div class="tag">{{ data.tag }}</div>
    </div>
    <div class="zj_box">
      <van-row>
        <van-col span="12">
          <div class="sort" style="float: left">
            全部章节({{ list.length }})
          </div>
        </van-col>
        <van-col span="12">
          <div class="sort" @click="sortFn">
            {{ sort ? "升序" : "降序" }}
            <van-icon size="20px" class="sortTop" name="exchange" />
          </div>
          <div class="sort" @click="chListFn" style="margin: 0 2px">
            <van-icon size="20px" class="sortTop" name="apps-o" />
          </div>
        </van-col>
      </van-row>
    </div>
    <div class="list_box">
      <van-row>
        <van-col :span="listSize" v-for="(item, i) in list" :key="i">
          <div class="list_item">
            <router-link
              :to="{ name: 'Detail', query: { url: item.url, num: item.num } }"
              class="span"
              >{{ item.tit }}</router-link
            >
          </div>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script>
import "../../assets/scss/about.scss";
import { mhListApi } from "@/api/api";


export default {
  name: "AboutIndex",
  data() {
    return {
      data: {
        introduce: "",
      },
      list: [],
      sort: true,
      startJson: {
        name: "",
        url: "",
      },
      isAllShowTips: false,
      backUrl: null,
      listSize: 8,
      isLike: false,
    };
  },
  methods: {
    back() {
      this.$router.push({ path: this.backUrl || "/" });
    },
    init() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let { url } = this.$route.query;
      window.sessionStorage.setItem("parentUrl", url);
     
      mhListApi(url).then((res) => {
        let { data, list, code } = res.data;
        if (code === 0) {
          list.map((v) => {
            v.tit = v.num.substring(0, 11);
          });
          this.startJson = {
            name: list[0].tit,
            url: list[0].url,
          };
          window.sessionStorage.setItem("mhList", JSON.stringify(list));
          this.data = data;
          this.list = list;
          

          
        }
        this.$toast.clear();
      });
    },
    sortFn() {
      this.sort = !this.sort;
      let arr = this.list;
      this.list = [];
      this.list = arr.reverse();
    },
    chListFn() {
      this.listSize == 8 ? (this.listSize = 12) : (this.listSize = 8);
    },
    start() {
      let { url } = this.startJson; //这个url 类似id
      this.$router.push({
        path: "/detail",
        query: {
          url: url,
        },
      });
    },

    //添加收藏
    likeAndNotLike() {
      this.isLike = !this.isLike;
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }
      if(this.isLike){
        this.$toast('加入书架');
      }else{
        this.$toast('取消加入');
      }

      //  被like传入的值
      if (this.isLike) {
        
       let data = this.data;
       
       localStorage.setItem('shoucan', JSON.stringify(data));
      let oldItems = JSON.parse(localStorage.getItem('itemsArray')) || [];
      var newItem = this.data;
      oldItems.push(newItem);
      localStorage.setItem('itemsArray', JSON.stringify(oldItems));
      }
    },
    imgErr() {
      let imgBg = document.querySelector(".img_box .img");
      imgBg.style.background = "#fb7299b5";
    },
  },
  created() {
    this.init();
  },
  computed: {
    isHouse: function () {
      return false;
    },
    favoriteText() {
      return this.isLike ? "已进书架" : "加入书架";
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.name === "home" || from.name === "list") {
        vm.backUrl = from.path;
      }
    });
  },
};
</script>
